html,
body {
  height: 100vh;
  min-width: 100%;
  font-size: 12px;
}

.container {
  height: 100vh;
  min-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to top right, hsl(240, 73%, 65%) , hsl(273, 75%, 66%));
}

.card {
  flex-direction: row;
  width: 1000px;
  z-index: 1;
  height: 500px;
  border-radius: 2em;
}

.acc-p {
  margin: 0;
}

.right-side {
  padding: 5em 5em 5em 5em;
  width: 495px;
  height: 500px;
}

.left-side {
  background-image: url(./images/bg-pattern-desktop.svg);
  background-position: 120% 70%;
  background-repeat: no-repeat;
}

#mainimg {
  position: relative;
  top: 80px;
  right: 85px;
  z-index: -1 ;
}

#box {
  position: relative;
  right: 100px;
  bottom: 145px;
  z-index: 1;
}

.accordation-h1 {
  margin-bottom: 1em;
}

/* Accordination */

.accordion-item {
  border: none;
}

.accordion-button {
  border-bottom: 1px solid gray;
}

/*  Use Js to fix the focus problem/active

.accordion-button:focus {
  border-bottom: none;
}

*/

.accordion-body {
  border-bottom: 1px solid gray;
}

.accordion-button:focus {
  font-weight: 700;
}

.accordion-button:hover {
  font-weight: 700;
  color: #ff9271;
}

.accordion-button:not(.collapsed) {
  background-color: transparent !important;
  color: inherit;
}

button.accordion-button:focus{
  box-shadow: none;
  outline: none; 
}

@media (max-width: 400px){

.container {
  min-width: 100%;
  min-height: 100%;
}

.card {
  flex-direction: column;
  width: 80%;
  height: 75%;
  z-index: 1;
  height: auto;
  border-radius: 2em;
  position: relative;
  top: 50px;
}

.right-side {
  padding: 5em 5em 2em 5em;
  width: 100%;
  height: 50%;
  background-image: url(./images/bg-pattern-mobile.svg);
  background-repeat: no-repeat;
  background-position: 15px -30px;
}

.left-side {
  
  background-position: right;
  height: 0px;
  width: 100%;
}

#mainimg {
  position: relative;
  top: -95px;
  right: -30px;
  z-index: -1 ;
  width: 75%;
}

.acc-p {
  font-size: 0.6em;
}

#box {
  position: relative;
  right: 175px;
  bottom: 75px;
  z-index: 1;
  width: 25%;
}

.accordation-h1 {
  margin-top: 1em;
  margin-bottom: 1em;
  text-align: center;
}

/* Accordination */

.accordion-item {
  border: none;
}

.accordion-button {
  border-bottom: 1px solid gray;
}

/*  Use Js to fix the focus problem/active

.accordion-button:focus {
  border-bottom: none;
}

*/

.accordion-body {
  border-bottom: 1px solid gray;
}

.accordion-button:focus {
  font-weight: 700;
}

.accordion-button:hover {
  font-weight: 700;
  color: #ff9271;
}

.accordion-button:not(.collapsed) {
  background-color: transparent !important;
  color: inherit;
}

button.accordion-button:focus{
  box-shadow: none;
  outline: none; 
}
}